import Swiper from "swiper";
import { Grid, Navigation } from 'swiper/modules';

function sliderProducts() {
  new Swiper('.products-slider__slider .swiper', {
    modules: [Navigation, Grid],
    grabCursor: true,
    spaceBetween: 23,
    slidesPerView: 2,
    navigation: {
      prevEl: '.swiper-button-prev',
      nextEl: '.swiper-button-next',
    },
    grid: {
      rows: 2,
      fill: "row"
    },
    breakpoints: {
      1280: {
        slidesPerView: 4,
        spaceBetween: 43,
        grid: {
          rows: 1,
          fill: "row"
        },
      }
    }
  });
}
sliderProducts();